<script>

    import {HttpResetClass} from 'vue-client'
    import Vue from 'vue'
    export default {
        title: "附件",
        props: ['blodid', 'isremark', 'isusetype','takeimg','defname','isupload','f_usetype','edit'],
        data() {
            return {
                isDelete: '1', // 删除按钮，可以进行控制（权限等）
                fileInfoData: [], // 数据库存储的文件记录对象数组
                useType: '', // 使用类型
                fileRemark: '', // 文件说明
                row:{

                },
                showz:true,
                showx:true,
                shown:true,
                showa:true,
                showb:true,
                showc:true,
                showd:true,
                showe:true,
                showf:true,
                showg:true,
                showh:true,
                showi:true,
                showj:true,
                showk:true
            }
        },
        methods: {
            // 查询按钮事件

            async search() {
                let condition = `f_process_id = '${this.blodid}'`
                let http = new HttpResetClass();
                let res = await http.load('POST', this.$androidUtil.getProxyUrl() +'/rs/sql/getimage', {data: {condition: condition}}, {
                    resolveMsg: null,
                    rejectMsg: '获取图片路径失败'
                })
                let getpath = await http.load('POST',this.$androidUtil.getProxyUrl() +'/rs/sql/singleTable_OrderBy', {data: {items: 'value,name',tablename: 't_singlevalue',orderitem: 'name' ,condition: `name = '图片地址'`}},{resolveMsg: null, rejectMsg: '获取区县失败!'})
                let path = ''
                console.log('查询到的图片：', getpath)
                if (getpath.data[0].value) {
                     path = getpath.data[0].value
                }
                // this.params.rows.splice(index, 1)
                 if (res.data.length > 0) {
                    for (let row of res.data) {
                        if (row.f_type === '身份证正面') {
                            let temp = row.f_picture
                            let URL = temp.substring(temp.lastIndexOf(":\\") + 2)
                            this.row.f_downloadURL1 = path +''+ URL
                            this.showa = false
                        }
                        if (row.f_type === '身份证反面') {
                            let temp = row.f_picture
                            let URL = temp.substring(temp.lastIndexOf(":\\") + 2)
                            this.row.f_downloadURL2 = path +'' + URL
                            this.showb = false
                        }
                        if (row.f_type === '房屋证明材料') {
                            let temp = row.f_picture
                            let URL = temp.substring(temp.lastIndexOf(":\\") + 2)
                            this.row.f_downloadURL3 = path +'' + URL
                            this.showc = false
                        }
                        if (row.f_type === '营业执照') {
                            let temp = row.f_picture
                            let URL = temp.substring(temp.lastIndexOf(":\\") + 2)
                            this.row.f_downloadURL4 = path +'' + URL
                            this.showd = false
                        }
                        if (row.f_type === '用户报装勘查业务申请表') {
                            let temp = row.f_picture
                            let URL = temp.substring(temp.lastIndexOf(":\\") + 2)
                            this.row.f_downloadURL5 = path +'' + URL
                            this.showe = false
                        }
                        if (row.f_type === '燃气报装申请告知单') {
                            let temp = row.f_picture
                            let URL = temp.substring(temp.lastIndexOf(":\\") + 2)
                            this.row.f_downloadURL6 = path +'' + URL
                            this.showf = false
                        }
                        if (row.f_type === '供气合同') {
                            let temp = row.f_picture
                            let URL = temp.substring(temp.lastIndexOf(":\\") + 2)
                            this.row.f_downloadURL7 = path +'' + URL
                            this.showg = false
                        }
                        if (row.f_type === '工程安装验收及结算单') {
                            let temp = row.f_picture
                            let URL = temp.substring(temp.lastIndexOf(":\\") + 2)
                            this.row.f_downloadURL8 = path +'' + URL
                            this.showh = false
                        }
                        if (row.f_type === '点火记录单') {
                            let temp = row.f_picture
                            let URL = temp.substring(temp.lastIndexOf(":\\") + 2)
                            this.row.f_downloadURL9 = path +'' + URL
                            this.showi = false
                        }
                        if (row.f_type === '压力试验结果') {
                            let temp = row.f_picture
                            let URL = temp.substring(temp.lastIndexOf(":\\") + 2)
                            this.row.f_downloadURL10 = path +'' + URL
                            this.showj = false
                        }
                        if (row.f_type === '用户确认') {
                            let temp = row.f_picture
                            let URL = temp.substring(temp.lastIndexOf(":\\") + 2)
                            this.row.f_downloadURL11 = path +'' + URL
                            this.showk = false
                        }
                        if (row.f_type === '用户确认') {
                          let temp = row.f_picture
                          let URL = temp.substring(temp.lastIndexOf(":\\") + 2)
                          this.row.f_downloadURL12 = path +'' + URL
                          this.showk = false
                        }
                      if (row.f_type === '现场勘察照片1') {
                        let temp = row.f_picture
                        let URL = temp.substring(temp.lastIndexOf(":\\") + 2)
                        this.row.f_downloadURL20 = path +'' + URL
                        this.showz = false
                      }
                      if (row.f_type === '现场勘察照片2') {
                        let temp = row.f_picture
                        let URL = temp.substring(temp.lastIndexOf(":\\") + 2)
                        this.row.f_downloadURL21 = path +'' + URL
                        this.showx = false
                      }
                      if (row.f_type === '现场勘察照片3') {
                        let temp = row.f_picture
                        let URL = temp.substring(temp.lastIndexOf(":\\") + 2)
                        this.row.f_downloadURL22 = path +'' + URL
                        this.shown = false
                      }
                    }
                }
            },
            confirm(val,content,filename) {
                if (!content) {
                    this.$showAlert("请先选择图片","warning", 1500)
                    return
                }
                if (!filename) {
                    this.$showAlert("请先选择图片","warning", 1500)
                    return
                }
                let param={
                    f_type : val,
                    f_content:content,
                    f_filename:filename,
                    f_process_id:this.blodid
                }
                this.$resetpost(this.$androidUtil.getProxyUrl() +'/rs/logic/saveimage', param).then(() => {
                    // this.params.rows.splice(index, 1)
                    console.log('更新成功！')
                    this.search()
                })
            },
        },
        async ready() {
            this.search()
        },
        watch: {
            'blodid'(val) {
                if (val) {
                    console.log('观察blodid', val)
                    this.search()
                    this.row={},
                    this.showa=true
                    this.showb=true
                    this.showc=true
                    this.showd=true
                    this.showe=true
                    this.showf=true
                    this.showg=true
                    this.showh=true
                    this.showi=true
                    this.showj=true
